@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search {
  margin-bottom: 64px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

  
}

.coin-text {
  margin-bottom: 20px;
  text-align: center;

  -webkit-animation-name: moveInLeft;

          animation-name: moveInLeft; /* Using the moveinleft animation created with keyframes below */
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

.tool-tip {
  font-size: 16px;
  margin-bottom: 20px;

  -webkit-animation-name: moveInRight;

          animation-name: moveInRight;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

/* Creating animations with keyframes */
@-webkit-keyframes moveInLeft {
    /* Controlling the animation in time - 0% = start, 100% = finish */
    0% {
        opacity: 0;
        -webkit-transform: translateX(-10rem);
                transform: translateX(-10rem);
    }

    80% {
        -webkit-transform: translateX(1.5rem);
                transform: translateX(1.5rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}
@keyframes moveInLeft {
    /* Controlling the animation in time - 0% = start, 100% = finish */
    0% {
        opacity: 0;
        -webkit-transform: translateX(-10rem);
                transform: translateX(-10rem);
    }

    80% {
        -webkit-transform: translateX(1.5rem);
                transform: translateX(1.5rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

@-webkit-keyframes moveInRight {
    /* Controlling the animation in time - 0% = start, 100% = finish */
    0% {
        opacity: 0;
        -webkit-transform: translateX(10rem);
                transform: translateX(10rem);
    }

    80% {
        -webkit-transform: translateX(-1.5rem);
                transform: translateX(-1.5rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

@keyframes moveInRight {
    /* Controlling the animation in time - 0% = start, 100% = finish */
    0% {
        opacity: 0;
        -webkit-transform: translateX(10rem);
                transform: translateX(10rem);
    }

    80% {
        -webkit-transform: translateX(-1.5rem);
                transform: translateX(-1.5rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

@-webkit-keyframes moveInBottom {
    /* Controlling the animation in time - 0% = start, 100% = finish */
    0% {
        opacity: 0;
        -webkit-transform: translateY(40px);
                transform: translateY(40px);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

@keyframes moveInBottom {
    /* Controlling the animation in time - 0% = start, 100% = finish */
    0% {
        opacity: 0;
        -webkit-transform: translateY(40px);
                transform: translateY(40px);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 5px;
  border: none;
  background-image: linear-gradient(
    -225deg,
  #ac32e4 0%, 
  #7918f2 48%, 
  #4801ff 100%
  );
  color: #e2e2e2;
  font-size: 16px;

  -webkit-animation: moveInBottom 1s ease-out .75s;

          animation: moveInBottom 1s ease-out .75s; /* Last one is delay! */
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards; /* Automatiaclly add the 0% keyframe from start to the element! */
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

.coin-container {
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: center;
          justify-content: center;

